import { useState, useEffect } from "react";
import { addresses, TOKEN_DECIMALS } from "../../constants";
import { Link, SvgIcon, Popper, Button, Paper, Typography, Divider, Box, Fade, Slide } from "@material-ui/core";
import { ReactComponent as InfoIcon } from "../../assets/icons/info-fill.svg";
import { ReactComponent as ArrowUpIcon } from "../../assets/icons/arrow-up.svg";
import { ReactComponent as swoxTokenImg } from "../../assets/tokens/SWOX.svg";
import { ReactComponent as woxTokenImg } from "../../assets/tokens/WOX.svg";

import "./woxmenu.scss";
import { usdt } from "src/helpers/AllBonds";
import { useWeb3Context } from "../../hooks/web3Context";

import WoxImg from "src/assets/tokens/WOX.svg";
import SWoxImg from "src/assets/tokens/SWOX.svg";

const addTokenToWallet = (tokenSymbol, tokenAddress) => async () => {
  if (window.ethereum) {
    const host = window.location.origin;
    // NOTE (appleseed): 33T token defaults to sWOX logo since we don't have a 33T logo yet
    let tokenPath;
    // if (tokenSymbol === "WOX") {

    // } ? WoxImg : SWoxImg;
    switch (tokenSymbol) {
      case "WOX":
        tokenPath = WoxImg;
        break;
      default:
        tokenPath = SWoxImg;
    }
    const imageURL = `${host}/${tokenPath}`;

    try {
      await window.ethereum.request({
        method: "wallet_watchAsset",
        params: {
          type: "ERC20",
          options: {
            address: tokenAddress,
            symbol: tokenSymbol,
            decimals: TOKEN_DECIMALS,
            image: imageURL,
          },
        },
      });
    } catch (error) {
      console.log(error);
    }
  }
};

function WoxMenu() {
  const [anchorEl, setAnchorEl] = useState(null);
  const isEthereumAPIAvailable = window.ethereum;
  const { chainID } = useWeb3Context();

  const networkID = chainID;

  const SWOX_ADDRESS = addresses[networkID].SWOX_ADDRESS;
  const WOX_ADDRESS = addresses[networkID].WOX_ADDRESS;
  const USDC_ADDRESS = addresses[networkID].USDC_ADDRESS;

  const handleClick = event => {
    setAnchorEl(anchorEl ? null : event.currentTarget);
  };

  const open = Boolean(anchorEl);
  const id = "wox-popper";
  const usdtAddress = usdt.getAddressForReserve(networkID);
  return (
    <Box
      component="div"
      onMouseEnter={e => handleClick(e)}
      onMouseLeave={e => handleClick(e)}
      id="wox-menu-button-hover"
    >
      <Button id="wox-menu-button" size="large" variant="contained" color="secondary" title="WOX" aria-describedby={id}>
        <SvgIcon component={InfoIcon} color="primary" />
        <Typography>WOX</Typography>
      </Button>

      <Popper id={id} open={open} anchorEl={anchorEl} placement="bottom-start" transition>
        {({ TransitionProps }) => {
          return (
            <Fade {...TransitionProps} timeout={100}>
              <Paper className="wox-menu" elevation={1}>
                <Box component="div" className="buy-tokens">
                  
                  <Link
                    href={`https://exchange.puddingswap.finance/#/swap/${USDC_ADDRESS}/${WOX_ADDRESS}`}
                    target="_blank"
                    rel="noreferrer"
                  >
                    <Button size="large" variant="contained" color="secondary" fullWidth>
                      <Typography align="left">
                        Buy on PuddingSwap <SvgIcon component={ArrowUpIcon} htmlColor="#A3A3A3" />
                      </Typography>
                    </Button>
                  </Link>
                </Box>

                {isEthereumAPIAvailable ? (
                  <Box className="add-tokens">
                    <Divider color="secondary" />
                    <p>ADD TOKEN TO WALLET</p>
                    <Box display="flex" flexDirection="row" justifyContent="space-between">
                      <Button variant="contained" color="secondary" onClick={addTokenToWallet("WOX", WOX_ADDRESS)}>
                        <SvgIcon
                          component={woxTokenImg}
                          viewBox="0 0 32 32"
                          style={{ height: "25px", width: "25px" }}
                        />
                        <Typography variant="body1">WOX</Typography>
                      </Button>
                      <Button variant="contained" color="secondary" onClick={addTokenToWallet("sWOX", SWOX_ADDRESS)}>
                        <SvgIcon
                          component={swoxTokenImg}
                          viewBox="0 0 32 32"
                          style={{ height: "25px", width: "25px" }}
                        />
                        <Typography variant="body1">sWOX</Typography>
                      </Button>
                    </Box>
                  </Box>
                ) : null}
              </Paper>
            </Fade>
          );
        }}
      </Popper>
    </Box>
  );
}

export default WoxMenu;
